    focused(selectedValue: any) {
     if(this.no_orientacion!='')
     {

      const loading = this.loadingCtrl.create({
        spinner: 'bubbles',
        content: "Espere un momento,  Buscando información ......"
      });
      loading.present();


      this.items=[]


      this.authService.validacarnenov(this.no_orientacion).then((data2) => {
        var vec:any=(data2);
        var cad =vec.split('°');


        if(cad[1]=='undefined' ||cad[1]=='')
        {
          this.no_orientacion=''
          this.nombre=''
          this.items=[]
          this.msj.showError3('Error','No existe NOV / CARNÉ');
          loading.dismiss();

        }
        else
        {
          this.nombre=cad[1];
          this.items=[{nombre1:cad[1]}]
          loading.dismiss();
        }

      }, (err) => {
        this.no_orientacion=''
        this.items=[]
        this.nombre=''
        this.msj.showError3('Error','No existe NOV / CARNÉ');
        loading.dismiss();
    });







     }

    }


